.event-card {
  border: 1px solid #ccc;
  padding: 20px;
  margin: 10px 0;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.event-card.past {
  background-color: #f8d7da;
}

.event-card.present {
  background-color: #fff3cd;
}

.event-card.future {
  background-color: #d4edda;
}

.event-card h3 {
  margin: 0 0 10px;
}

.event-card p {
  margin: 5px 0;
}
